.contact-section {
    text-align: center;
  }
  
  .contact-form {
    max-width: 500px;
    margin: 30px auto;
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  .contact-form input,
  .contact-form textarea {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 6px;
    font-size: 1rem;
  }
  
  .contact-form button {
    padding: 10px;
    background-color: #00aaff;
    color: white;
    border: none;
    font-weight: bold;
    border-radius: 6px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .contact-form button:hover {
    background-color: #008ecc;
  }
  
  .success-msg {
    color: green;
    margin-top: 10px;
  }
  